@Component({
  selector: 'app-register',
  templateUrl: './register.page.html',
  styleUrls: ['./register.page.scss'],
})
export class RegisterPage implements OnInit {


  termosDeUso() {
    if (confirm(this.termoDefinicao)) {
      return true;
    } else {
      return false;
    }
  }

  termoDefinicao: string = "TERMOS E CONDI√á√ïES DE USO RO√áAEAST \n Art. 1¬∫ O doador (produtor rural, distribuidor, atacadistas e varejistas), declara estar ciente da responsabilidade e assume o compromisso de cumprir com as normas de rastreabilidade da INC 02/2018. \n Art. 2¬∫ Os doadores citados no artigo anterior, ao aceitarem os termos e condi√ß√µes de uso, declaram sob as penas da lei, manter registros dos insumos agr√≠colas aplicados na produ√ß√£o ou p√≥s-colheita por 18 meses e realizar a rastreabilidade, com registros, controle e identifica√ß√£o (conforme INC 02/2018). \n Art. 3¬∫ ao aceitarem os termos de uso, os doadores assumem a responsabilidade de entregar a doa√ß√£o aos benefici√°rios, seguindo os termos de qualidade e higieniza√ß√£o. \n Art. 4¬∫ os doadores assumem o compromisso de n√£o onerar por qualquer forma ou valor, o benefici√°rio da doa√ß√£o. \n Art. 5¬∫ os doadores, assumem o compromisso de entregar os produtos previamente definidos, a institui√ß√£o benefici√°ria, nos dias e hor√°rios pr√©-definidos. ¬ß1¬∫ as condi√ß√µes de entregas definidas no caput do artigo anterior, poder√£o ser revistas e realocadas, modificadas ou alteradas, em casos oriundos a estado de necessidade ou for√ßa maior. ¬ß2¬∫ ocorrendo qualquer hip√≥tese do paragrafo anterior, o doador assume n√£o realizar entrega de qualquer produto fora dos padr√µes de qualidade e consumo, reavaliando uma nova remessa de produtos. \n Art. 6¬∫ os benefici√°rios assumem o compromisso, de receber os produtos oriundos das doa√ß√µes, nos dias √© hor√°rios programados, passando a confirmar e avaliar a qualidade dos produtos entregues. \n Art. 7¬∫ os benefici√°rios dos produtos doados firmam o compromisso de fazer a destina√ß√£o dos produtos, aos necessitados em situa√ß√£o de precariedade ou qualquer dificuldade social, sem custo ou √¥nus. \n Art. 8¬∫ os cadastrados na plataforma Ro√ßaEast, autorizam a utiliza√ß√£o dos dados cadastrais inseridos no sistema no momento da confirma√ß√£o e aceita√ß√£o destes termos e condi√ß√µes de uso. ¬ß1¬∫ os membros e usu√°rios da plataforma Ro√ßaEast, autorizam o uso de imagens audiovisuais, fotografias, textos, logo e marcas, nome fantasia e raz√µes sociais, em campanhas com fim social, exposi√ß√£o na plataforma digital, p√¥ster, reportagens associadas a plataforma em ambientes midi√°ticos, jornal√≠sticos ou qualquer plataforma de redes sociais ligadas a Ro√ßaEast. \n Art. 9¬∫ a Ro√ßaEats fica isenta de qualquer responsabilidade sobre as doa√ß√µes entregues, seu tipo, quantidade e qualidade, ou quaisquer danos que por motivo de estado de necessidade ou for√ßa maior vierem a acontecer com o transporte desta. Ciente dos termos e condi√ß√µes acima, aceito o uso da plataforma Ro√ßaEats, a fim de cumprir com as condi√ß√µes pr√©-estabelecidas e qualquer outras que vierem a ser introduzidas ou alteradas durante o per√≠odo de cadastramento na plataforma. Declaro ainda, serem verdadeiras as informa√ß√µes e estou ciente da aplica√ß√£o das penalidades prescritas no Art. 299 do C√≥digo Penal Brasileiro, caso n√£o haja veracidade."
 
  // textos inicio
  tituloHeader: string = "COLHENDO O MELHOR DE N√ìS!";

  // textos projeto
  tituloProjeto: string = "Cadastro";

  // textos footer
  tituloFooter: string = "SOBRE O RO√áA EATS";
  textoFooter: string = "Procurando sempre satisfazer a necessidade de uma alimenta√ß√£o saud√°vel, direto do campo para sua casa.";
  
  user = new User();
  address = new Address();

  states: any;

  slideOpts = {
    initialSlide: 1,
    speed: 400
  };

  constructor(public alertController: AlertController, public ibgeProvider: IBGEProvider, public userService: UserService, public viaCep: ViaCepProvider, public openStreetMap: OpenStreetMapProvider, private router: Router) { }

  ngOnInit() {
    this.ibgeProvider.getStates().subscribe(data => {
      this.states = data;
    });
  }
  redirectInicio(){
    return window.location.href = 'http://ec2-52-67-30-15.sa-east-1.compute.amazonaws.com/';
  }
  redirectLogin(){
    return window.location.replace('http://ec2-52-67-30-15.sa-east-1.compute.amazonaws.com/login');
  }
  redirectRegister(){
    this.router.navigate(['/register'])
    return window.location.href = 'http://ec2-52-67-30-15.sa-east-1.compute.amazonaws.com/register';
  }

  getAddress() {
    if (this.address.postalCode != null) {
      this.viaCep.callService(this.address.postalCode)
        .subscribe(
          data => {
            this.address.postalCode = data.cep;
            this.address.address = data.logradouro;
            this.address.city = data.localidade;
            this.address.neighborhood = data.bairro;
            
            var state = this.states.find(x => x.sigla == data.uf);

            this.address.state = state.nome;

            this.openStreetMap.callService(`${this.address.address}, ${this.address.neighborhood}, ${this.address.city}, ${this.address.state}, brasil`).subscribe(
              data => {
                if (data) {
                  this.address.lat = data[0].lat;
                  this.address.lon = data[0].lon;
                }
              }
            )
          }
        );
    }
  }

  async register() {

    this.user.address = this.address;

    this.userService.register(this.user).subscribe(data => {
      console.log(JSON.stringify(this.user));

      this.router.navigate(['/dashboard']);
    });    
  }

  matchPassword(): any {
    if (this.user.password === this.user.confirmPassword) {
      return null;
    }

    return {
      isValid: false
    };
  }

  async presentAlert(subHeader, message) {
    const alert = await this.alertController.create({
      header: 'Alert',
      subHeader: subHeader,
      message: message,
      buttons: ['OK']
    });

    await alert.present();
  }
}
